/**
 * wordpress dependencies
 */
import { useBlockProps, InnerBlocks } from '@wordpress/block-editor';

function Save( props ) {
	const {
		attributes: {},
	} = props;
	const blockProps = useBlockProps.save( {
		className: 'gutenberghub-tab-button',
	} );
	return (
		<div { ...blockProps }>
			<InnerBlocks.Content />
		</div>
	);
}
export default Save;
