/**
 * wordpress dependencies
 */
import { isEmpty } from 'lodash';
import { useEffect } from '@wordpress/element';
import { useDispatch, useSelect } from '@wordpress/data';
import { useBlockProps, InnerBlocks } from '@wordpress/block-editor';
/**
 * internal imports
 */
import { getParentBlock } from '../../utils';
import classNames from 'classnames';

function Edit( props ) {
	const { clientId, isSelected } = props;

	const blockIndex = useSelect( ( select ) => {
		const index = select( 'core/block-editor' ).getBlockIndex( clientId );

		return index;
	} );
	const blockProps = useBlockProps( {
		className: classNames( 'gutenberghub-tab-button', {
			'gutenberghub-active-tab':
				props.context.activeTabIndex === blockIndex,
		} ),
	} );
	const { updateBlockAttributes } = useDispatch( 'core/block-editor' );

	const selectedBlockClientId = useSelect( ( select ) =>
		select( 'core/block-editor' ).getSelectedBlockClientId()
	);
	const tabBlock = getParentBlock( selectedBlockClientId, 'ghub/tab-button' );

	const selectedBlockIndex = useSelect( ( select ) => {
		const index = select( 'core/block-editor' ).getBlockIndex(
			tabBlock?.clientId
		);

		return index;
	} );
	useEffect( () => {
		if ( isSelected || ! isEmpty( tabBlock ) ) {
			const parentBlock = getParentBlock(
				tabBlock?.clientId ?? clientId,
				'ghub/tabs-container'
			);

			if ( parentBlock ) {
				updateBlockAttributes( parentBlock.clientId, {
					activeTabIndex: selectedBlockIndex,
				} );
			}
		}
	}, [ isSelected, tabBlock ] );

	const template = [
		[
			'core/paragraph',
			{
				placeholder: 'Tab Title',
				style: {
					spacing: {
						margin: {
							top: '0',
							right: '0',
							bottom: '0',
							left: '0',
						},
					},
				},
			},
		],
	];
	return (
		<div { ...blockProps }>
			<InnerBlocks template={ template } />
		</div>
	);
}
export default Edit;
